@charset "utf-8";

/* ✅ 스크롤바 생겨도 레이아웃 점프 방지 */
html { scrollbar-gutter: stable; }
/* ⛳ 지원 안 하는 브라우저 폴백 */
@supports not (scrollbar-gutter: stable) {
  html { overflow-y: scroll; }  /* 항상 세로 스크롤바 자리 확보 */
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

/* 로고 */
header {
  display: flex;
  width: 100%;
  height: 70px;
  border-bottom: 1px solid #000;
  position: sticky;
  top: 0;
  background-color: #fff;
  z-index: 2000;
}

header .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
}

header .logo a {
  display: flex;
  align-items: center;
}

header .logo a img {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  object-fit: contain;
}

header .logo a h2 {
  margin-left: 10px;
  font-size: 18px;
  font-weight: 700;
}

/* 메뉴 */
header .menu {
  display: flex;
  justify-content: space-evenly;
  width: 80%;
  line-height: 70px;
}

.menu {
  display: flex;
}

.menu li {
  width: 20%;
  font-size: 18px;
  margin-left: 50px;
}

/* 반응형 추가 */
@media screen and (max-width: 1024px) {
  .menu li {
    font-size: 16px;
    margin-left: 30px;
  }
}

@media screen and (max-width: 768px) {
  header {
    height: 60px;
  }

  .menu li {
    font-size: 12px;
    margin-left: 20px;
  }
}

@media screen and (max-width: 480px) {
  header .logo {
    width: 30%;
  }

  header .logo a h2 {
    font-size: 18px;
  }

  header .menu {
    justify-content: space-between;
  }

  .menu li {
    font-size: 10px;
    margin-left: 10px;
  }
}